  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-bottom: 24px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__reviews {
